<template>
  <page
    @on-ready="onReady"
    @on-show="onShow"
    ref="page"
    class="plan-detail-page common-page"
    :headerHeightVW="headerHeightVW"
    scrollbar
  >
    <van-nav-bar slot="header" id="header" title="工单详情" left-arrow @click-left="onClickLeft">
    </van-nav-bar>

    <div class="detail-container">
      <div class="detail-header">
        <div class="detail-cell">
          <div class="detail-no">{{detail.no}}</div>
          <div class="detail-status">{{statusEnum[detail.status]}}</div>
        </div>
        <div class="detail-cell">
          <div class="detail-time1">计划完成时间: {{detail.finishTime}}

            <span style="color: red">{{detail.expireTime}}</span>
          </div>
          <!-- <div class="detail-time2">{{detail.timeConsuming}} min</div> -->
        </div>
        <div class="detail-cell">
          <div class="item-address">巡检点：{{detail.line}} / {{detail.station}}</div>
        </div>
        <div class="detail-cell">
          <div class="item-time1">检修周期：{{detail.planType}}</div>
        </div>
      </div>
    </div>

    <van-cell-group style="margin-top: 12px;">
      <van-cell @click="handleForm(item, index)" clickable :title="item.title" :value="formStatusEnum[item.status]" is-link v-for="(item, index) in detail.formList" :key="item.id"></van-cell>
    </van-cell-group>

    <van-cell-group>
      <van-cell title="维修过程" is-link clickable @click="handleRecord"></van-cell>
    </van-cell-group>

    <div class="blank-empty"></div>

    <template slot="blank">
      <div class="footer-action" v-if="detail.status == 'assigned'">
        <van-button @click.native="handleReceive" class="action-item-main" color="#108EE9">接报</van-button>
      </div>

      <div class="footer-action" v-if="detail.status == 'pending_sign'">
        
        <div class="action-item" @click="handleForward">
          <i class="action-item-icon icon-zhuanfa"></i>
          <span>转发</span>
        </div>
        <van-button @click.native="handleSign" class="action-item-main" color="#108EE9" >签到</van-button>
      </div>
      <div class="footer-action" v-if="detail.status == 'waiting_repair'">
        
        <div class="action-item" @click="handleForward">
          <i class="action-item-icon icon-zhuanfa"></i>
          <span>转发</span>
        </div>
        <van-button :disabled="!canSubmit" @click.native="handleSubmit" class="action-item-main" color="#108EE9" >提交</van-button>
      </div>
    </template>
  </page>
</template>

<script>
import Page from '../../components/Page'
import navigate from '../../utils/navigate.js'
import io from '../../api'
import db from '../../db'
import native from '../../utils/native.js'
import {statusEnum, formStatusEnum} from '../../utils/enum.js'
import {uploadImage} from '../../utils/upload.js'

export default {
  components: {
    Page,
  },
  data: () => ({
    headerHeightVW: 0,
    id: null,
    detail: {},
    online: false,
    user: JSON.parse(localStorage.getItem('user')),
    statusEnum: statusEnum,
    formStatusEnum: formStatusEnum,
    canSubmit: false
  }),
  methods: {
    onReady (header, main) {
      this.header = header

      this.headerHeightVW = (this.header.h + 1) * 100 / this.header.w

      this.id = navigate.getParam('id')

      this.loadDetail()

    },
    onShow () {
      this.loadDetail()
    },
    onClickLeft() {
      navigate.back()
    },
    handleRepair () {
      navigate.open('repair', {orderid: this.id})
    },
    handleSign () {
      navigate.open('photosign', {orderid: this.id})
    },
    loadDetail () {
      this.$toast.loading({
        duration: 0,
        loadingType: 'spinner'
      })

      db.getOrderDetail(this.id).then(data => {
        if (data.id == this.id) {
          this.online = false
          this.detail = {...data}

          // alert(JSON.stringify(this.detail))
          
          // 数据同步
          // if (native.connection()) {

          //   if (!data.signPhoto) {
          //     data.signPhoto = []
          //   }

          //   uploadImage(data.signPhoto.map(item => ({url: item}))).then(sp => {
          //     data.signPhoto = sp

          //     io.uploadPlan({...data}).catch(msg => {
          //       this.$toast('数据同步失败')
          //     })
          //   })
          // }

          this.canSubmit = native.connection()

          for (let i = 0; i < this.detail.formList.length; i++) {
            if (this.detail.formList[i].status != 'executed') {
              this.canSubmit = false
              break
            }
          }
          
          this.$toast.clear()
        } else {
          this.online = true
          io.getPlanDetail({id: this.id}).then(data => {
            this.detail = {...data}

            // 如果是待维修状态或者待签到存储到本地
            if (this.detail.status == 'waiting_repair' || this.detail.status == 'pending_sign') {
              db.saveOrder({...this.detail, type: 2}).then(() => {
                this.$toast.clear()
              })
            } else {
              this.$toast.clear()
            }
          }).catch(msg => {
            this.$toast(msg)
          })
        }
      })
    },

    handleReceive () {
      this.$toast.loading({
        duration: 0,
        forbidClick: true,
        loadingType: 'spinner',
        message: '正在接报...'
      })
      
      io.receivePlanOrder({
        id: this.detail.id
      }).then(() => {
        
        // 将该故障单存储到本地
        db.saveOrder({...this.detail, status: 'pending_sign', type: 2}).then(() => {
          this.$toast.clear()
          this.loadDetail()
        }).catch(msg => {
          this.$toast(msg)
        })
      }).catch(msg => {
        this.$toast(msg)
      })
      
    }, 

    handleForward () {

      if (native.connection()) {
        navigate.open('organization', {id: this.id, type: 2})
      } else {
        alert('网络无法连接，转发请求失败')
      }
    },

    handleClearup () {
      if (api.connectionType == 'none') {
        alert('网络无法连接，销项请求失败')
      } else {
        navigate.open('clearup', {orderid: this.id})
      }
    },

    handleHang () {
      if (api.connectionType == 'none') {
        alert('网络无法连接，挂起请求失败')
      } else {
        navigate.open('hang', {orderid: this.id})
      }
    },

    handleForm (item, index) {
      let readonly = this.detail.status != 'waiting_repair'

      navigate.open('form_detail', {id: this.id, index, readonly})
    },

    handleRecord () {
      navigate.open('orderRecord', {id: this.detail.id, type: 2})
    },

    handleSubmit () {
      if (native.connection()) {

        db.getOrderDetail(this.id).then(data => {
          if (!data.signPhoto) {
            data.signPhoto = []
          }

          uploadImage(data.signPhoto.map(item => ({url: item}))).then(sp => {
            data.signPhoto = sp

            io.uploadPlan({...data}).catch(msg => {
              io.submitPlan({
                id: this.id,
                formList: data.formList
              }).then(() => {
                db.removeOrder(this.id).then(() => {
                  navigate.back()
                })
              }).catch(msg => {
                this.$toast(msg)
              })
            })
          })
        })
      }
    }
  }
}
</script>

<style lang="scss">
.detail-container{
  .detail-title{
    padding-bottom: 0;
    &::after{
      display: none;
    }
  }

  .van-cell-group{
    margin-top: 12px;
  }

  .detail-header{
    background: #fff;
    padding: 13px 16px;
    .detail-cell{
      display: flex;
      justify-content: space-between;

      &.detail-cell{
        margin-top: 8px;
      }
    }

    .detail-no{
      font-size: 16px;
      line-height: 20px;
    }

    .detail-status{
      font-size: 16px;
      line-height: 20px;
      color: #FFB105;
    }

    .detail-time1{
      line-height: 18px;
      font-size: 14px;
      color: #939496;
    }

    .detail-time2{
      line-height: 18px;
      font-size: 14px;
      color: #1B82D2;
    }

    .item-address{
      padding-left: 30px;
      position: relative;
      &::before{
        width: 16px;
        height: 16px;
        display: block;
        background: url('../../assets/images/order/address.png') no-repeat;
        background-size: 100% 100%;
        content: '';
        position: absolute;
        left: 0;
        top: 0;
      }
    }

    .item-time1{
      padding-left: 30px;
      position: relative;
      &::before{
        width: 16px;
        height: 16px;
        display: block;
        background: url('../../assets/images/order/time.png') no-repeat;
        background-size: 100% 100%;
        content: '';
        position: absolute;
        left: 0;
        top: 0;
      }
    }
  }
}
</style>
